import http from "../http-common"

class ClientDataService 
{

    getAll(): Promise<any> {
        return http.get('/person/clients');
    }

    create(data: any): Promise<any> {
        return http.post('/person',data);
    }

    createReferences(arrayData: any): Promise<any> {
        return http.post('/person/reference',arrayData);
    }

    show(client_id: any)
    {
        return http.get(`/person/${client_id}`)
    }

    getReferences(client_id:any)
    {
        return http.get(`/person/references/${client_id}`)
    }

}
export default new ClientDataService();