import http from "../http-common"

class CityDataService {
    getAll(): Promise<any> {
        return http.get('/city');
    }

    create(data: any): Promise<any>{
        return http.post('/city',data)
    }

    delete(id: number){
        return http.delete(`/city/${id}`)
    }
    
}

export default new CityDataService();