import http from "../http-common"

class AddressDataService 
{

    // getAll(): Promise<any> {
    //     return http.get('/address');
    // }

    create(arrayData: any[]): Promise<any> {
        return http.post('/address',arrayData);
    }
    getPersonAdrress(person_id:any): Promise<any>{
        return http.get(`/address/${person_id}`)
    }


}

export default new AddressDataService();